// 引入分类初始化数据
import {index} from "../../APIs/index.js"
export default {
	data() {
		return {
			swActive:0,
			swiperData:[],
			typeMenuList:[],
			hotList:[],//热门榜单
			bouqList:[],//精品推荐
			firstList:[],//新品首发
			protList:[],//促销单品
		}
	},
	onReady() {
		uni.showLoading({
			title:"加载数据中……",
			mask:true
		})
		index().then(res=>{
			let [err,{data}] = res;
			uni.hideLoading()
			if(data.code==1){
				this.typeMenuList = data.data.menus
				this.swiperData=data.data.carousel
				this.hotList=data.data.hotList
				this.bouqList=data.data.bouqList
				this.firstList=data.data.firstList
				this.protList=data.data.protList
			}else{
				uni.showToast({
					title:data.msg,
					icon:'none'
				})
			}
		})
	},
	methods: {
		toPage(url){
			uni.navigateTo({
				url:url
			})
		},
		// 点击顶部搜索跳转到搜索页面
		toSearch(){
			uni.navigateTo({
				url:"../search/search"
			})
		},
		// 点击跳转到新闻页
		toNews(){
			uni.navigateTo({
				url:"../news/news"
			})
		},
		toDetail(e){
			let id=e.currentTarget.dataset.id;
			uni.navigateTo({
				url:'../detail/detail?id='+id,
				
			})
		},
	}
}
